<template>
  <div class="help-box">
    <div class="help-content">
      <div class="help-left">
        <ul>
          <li :class="{'titel hover':'0' ==index}" @click="clickfun(0)"></li>
          <li :class="{'titel hover':'1' ==index}" @click="clickfun(1)"></li>
          <li :class="{'titel hover':'2' ==index}" @click="clickfun(2)"></li>
          <li :class="{'titel hover':'3' ==index}" @click="clickfun(3)"></li>
          <li :class="{'titel hover':'4' ==index}" @click="clickfun(4)"></li>
          <li :class="{'titel hover':'5' ==index}" @click="clickfun(5)"></li>
          <li :class="{'titel hover':'6' ==index}" @click="clickfun(6)"></li>
          <li :class="{'titel hover':'7' ==index}" @click="clickfun(7)"></li>
          <li :class="{'titel hover':'8' ==index}" @click="clickfun(8)"></li>
          <li :class="{'titel hover':'9' ==index}" @click="clickfun(9)"></li>
          <li :class="{'titel hover': '10' ==index}" @click="clickfun(10)"></li>
        </ul>
      </div>
      <div class="help-right">
        <div class="search">
          <div>{{ litel_text }}</div>
        </div>
        <div class="search-list">
          <div class="list-item">
            <p v-for="item in dataproblem" :key="item">{{item.text}}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

