            ". . . "
        },
        { text: ". " },
        { text: ". " },
        { text: ". " },
        { text: ". " },
        { text: ". " },
        { text: ". logo" },
        { text: ". IP" },
        { text: ". USB" },
        { text: ". IP" },
        { text: ". USB" },
        { text: ". " },
        { text: ". " },
        { text: ". " },
        { text: ". " },
        { text: ". " },
        { text: ". " },
        { text: ". " },
        { text: ". " },
        { text: ". " },
        { text: ". " },
        { text: ". " },
        { text: ". " },
        {
