        },
        { text: ". " },
        { text: ". " },
        { text: ". " },
        { text: ". " },
        { text: ". " },
        { text: ". " },
        { text: ". " },
        { text: ".  " },
        { text: ". " },
        { text: ". PosPal" },
        { text: ". " },
        {
