<style scoped lang="scss">
.help-box {
  width: 100%;
  padding: 100px 0;
  background: #f7fafc;
  font-weight: 400;
  font-size: 20px;
  color: #707070;
  .help-content {
    max-width: 1300px;
    margin: 0 auto;
    display: flex;
    .help-left {
      width: 297px;
      font-size: 20px;
      line-height: 43px;
      color: #707070;
      .titel {
        border-bottom: 1px solid #707070;
        padding-left: 32px;
        background: #d3d7dc;
      }
      li {
        border-bottom: 1px solid #aab2bd;
        padding-left: 32px;
        cursor: pointer;
        &:hover {
          // border-right: 5px solid #1352ae;
          // border-bottom: 1px solid #707070;
          background: #d3d7dc;
        }
      }
      .hover {
        border-right: 5px solid #1352ae;
      }
    }
    .help-right {
      padding-left: 53px;
      flex: 1;
      color: #707070;
      .search {
        line-height: 38px;
        background: #d3d7dc;
        padding-left: 13px;
      }
      .search-list {
        padding-left: 13px;
        .list-item {
          line-height: 44px;
        }
      }
    }
  }
}
</style>


